<template>
  <footer v-if="menuList&&menuList.length>0" class="xy-nav">
    <dl
      v-for="(item,index) in menuList"
      :key="index"
      :class="{activeMenu: footNum == index}"
      @click="toLink(item,index)"
    >
      <dt :class="footNum == index ? item.activeIcon : item.icon">
        <div v-if="index === 1 && msgCount>0" class="num">{{ msgCount }}</div>
        <!-- <div v-if="index === 3 && item.showMessage === true && msgCount>0" class="num">{{ msgCount }}</div> -->
      </dt>
      <dd
        :class="footNum == index ? 'menu-text-active':''"
      >{{ item.meta.title }}</dd>
    </dl>
  </footer>
</template>
<script type='es6'>
import { mapState } from 'vuex'

export default {
  name: '',
  // props: {
  //   footMeun: {
  //     type: Array,
  //     default: () => []
  //   }
  // },
  data() {
    return {
      // msgCount: 0,
      menuList: []
    }
  },
  computed: {
    // 取数据
    ...mapState({
      userInfo: state => state.mh.mhUserInfo,
      msgCount: state => state.router.msgCount
    })
  },
  watch: {
    menuList(value) {
      if (value && value.length > 0) {
        this.menuList = value
      }
    }
  },

  created() {
    this.footNum = this.$store.state.router.footNum
    this.menuList = this.$store.state.router.footMeun
    if (this.menuList.length === 0 || JSON.parse(window.localStorage.getItem('isFirst')) === 1) {
      console.log('footNum==Icon===', this.footNum)
      this.getBottomCdList()
    } else {
      this.menuList = this.menuList
    }
  },
  methods: {
    // setMenuBottom(menuList) {
    //   this.menuList = menuList
    // },
    getMenuBottom() {
      if (this.menuList.length > 0) {
        this.menuList = this.menuList
        console.log('menuList==menuList===', this.menuList)
        // if (this.isActive === '') {
        // this.isActive = this.menuList[0].component
        // }
      }
    },
    getBottomCdList() {
      this.$store.commit('router/SET_Foot_Meun', '')
      this.$store.dispatch('mh/getBottomCdList', {
        ryId: this.userInfo.id
      }).then(res => {
        // res = JSON.parse(res)
        window.localStorage.setItem('isFirst', JSON.stringify(0))
        if (res !== '' && res != null) {
          let list = []
          let childrens = []
          list = JSON.parse(res)
          if (list.router.length === 0) {
            this.$toast.fail('暂无数据！')
          } else {
            for (let i = 0; i < list.router.length; i++) {
              childrens = list.router[i].children
              if (this.userInfo.zzxz === 3 && list.router[i].component === 'SJDBCD_GYS') {
                for (let j = 0; j < childrens.length; j++) {
                  if (j !== 0 && j % 2 === 0 && j === Math.floor(childrens.length / 2)) {
                    childrens[j].activeIcon = childrens[j].icon
                  } else {
                    childrens[j].activeIcon = childrens[j].icon + '-active'
                  }
                }
                this.$store.commit('router/SET_Foot_Meun', childrens)

                break
              } else if (this.userInfo.zzxz === 5 && list.router[i].component === 'JBR_HOME') {
                for (let j = 0; j < childrens.length; j++) {
                  if (j !== 0 && j % 2 === 0 && j === Math.floor(childrens.length / 2)) {
                    childrens[j].activeIcon = childrens[j].icon
                  } else {
                    childrens[j].activeIcon = childrens[j].icon + '-active'
                  }
                }
                this.$store.commit('router/SET_Foot_Meun', childrens)
                console.log('555555555555======', childrens)

                break
              } else if (this.userInfo.zzxz === 4 && list.router[i].component === 'SJDBCD_DLJG') {
                for (let j = 0; j < childrens.length; j++) {
                  if (j !== 0 && j % 2 === 0 && j === Math.floor(childrens.length / 2)) {
                    childrens[j].activeIcon = childrens[j].icon
                  } else {
                    childrens[j].activeIcon = childrens[j].icon + '-active'
                  }
                }
                console.log('2222222222======', childrens)

                this.$store.commit('router/SET_Foot_Meun', childrens)
                break
              } else if (this.userInfo.zzxz === 4 && list.router[i].component === 'SJDBCD_GYS') {
                for (let j = 0; j < childrens.length; j++) {
                  if (j !== 0 && j % 2 === 0 && j === Math.floor(childrens.length / 2)) {
                    childrens[j].activeIcon = childrens[j].icon
                  } else {
                    childrens[j].activeIcon = childrens[j].icon + '-active'
                  }
                }
                this.$store.commit('router/SET_Foot_Meun', childrens)

                break
              } else {
                for (let j = 0; j < childrens.length; j++) {
                  if (j !== 0 && j % 2 === 0 && j === Math.floor(childrens.length / 2)) {
                    childrens[j].activeIcon = childrens[j].icon
                  } else {
                    childrens[j].activeIcon = childrens[j].icon + '-active'
                  }
                }
                this.$store.commit('router/SET_Foot_Meun', childrens)

                break
              }
            }
            console.log('childrens====================================', childrens)
            this.menuList = childrens
            this.$store.commit('router/SET_Foot_Meun', childrens)
          }
        } else {
          this.$store.commit('router/SET_Foot_Meun', [])
        }
      }).catch(error => {
        this.$toast.fail(error)
      })
    },
    toLink(item, index) {
      var self = this

      if (index === this.footNum) {
        return
      } else {
        this.$store.commit('router/SET_Foot_Num', index)
      }
      // console.log(item.component, '-----', this.isActive)
      if (item.needLogin) {
        if (!this.userInfo.id) {
          this.$toast.fail('请先登录！')
        }
      }
      if (item.type === 'url') {
        self.$router.push({
          name: 'GgIframe',
          params: { 'ggUrl': item.component, 'title': item.title }
        })
      } else {
        self.$router.push(item.component)
      }
    }
  }
}
</script>

<style lang="less" scoped>

</style>
