<template>
  <div class="kaoqin">
    <div class="nav-bar-white">
      <headerBar :header-data="headerData" />
    </div>
    <div class="kaoqin-head">
      <div class="kaoqin-userinfo-bg" />
      <div class="kaoqin-userinfo">
        <div class="userinfo-tx" @click="showImage(userInfo.tx_url?userInfo.tx_url:avatar)">
          <van-image
            :src="
              userInfo.tx_url?userInfo.tx_url:avatar"
            fit="cover"
            :round="true"
          />
        </div>
        <div class="userinfo-name">
          <div class="name">{{ userInfo.name }}</div>
          <div class="info-flex">
            <div class="info">
              <i class="iconfont icondailijigou" />
              {{ userInfo.bmmc }}
            </div>
            <div class="info">
              <i class="iconfont iconshouji" />
              {{ userInfo.sj }}
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="sale-main">
      <van-tabs v-model="active" @click="onClick">
        <van-tab>
          <template #title>
            <div class="saletab-flex">
              <div class="saletab-icon"><i class="iconfont iconshangxiabandaka" /></div>
              <div class="saletab-item">
                <div class="saletab-tit">上下班打卡</div>
                <!-- <div v-if="dkid" class="saletab-state">已打卡</div>
                <div v-else class="saletab-state">未打卡</div> -->
              </div>
            </div>
          </template>

          <!-- 上下班打卡 begin -->
          <div class="work-clock-flex">

            <div class="work-clock" @click="dkClick(0)">
              <div class="clock-icon on" />

              <div v-if="amPageData.wz" class="clock-date">
                <div class="time">{{ getTime(amPageData.sj,1) }}</div>
                <div class="day">{{ getTime(amPageData.sj,2) }}</div>
              </div>
              <!-- <div class="clock-date">{{ amPageData.sj }}</div> -->
              <div class="clock-tit">上班<span v-if="amPageData.wz" class="ydk">已打卡</span></div>
            </div>

            <div class="work-clock" @click="dkClick(1)">
              <div class="clock-icon off" />
              <div v-if="pmPageData.wz" class="clock-date">
                <div class="time">{{ getTime(pmPageData.sj,1) }}</div>
                <div class="day">{{ getTime(pmPageData.sj,2) }}</div>
              </div>
              <!-- <div class="clock-date">{{ pmPageData.sj }}</div> -->
              <div class="clock-tit">下班<span v-if="pmPageData.wz" class="ydk">已打卡</span></div>
            </div>

          </div>
          <!-- 上下班打卡 end -->
          <!-- <div class="work-current-time">当前时间：<span>{{ nowData }}</span></div> -->
          <!--  位置 begin -->
          <div class="work-address">
            <div class="address-tit"><i class="iconfont icondizhi" />位置</div>
            <div class="address-info">{{ locationMsg.address1 }} </div>
          </div>
          <!--  位置 end -->

          <div class="work-address">
            <div class="address-tit"><i class="iconfont iconlishijilu" />历史<div style="float:right" @click="goMore(1)">更多> </div></div>
            <div v-for="(item,index) of dkls" :key="index" class="address-info">{{ index+1 }}、{{ zhTime(item.ccdksj) }} {{ item.ccdkwz }}</div>
          </div>

          <!-- 打卡成功 beign -->
          <!-- <div class="workclock-state">
            <div class="state-icon success" />
            <div class="state-text">上班打卡成功</div>
          </div> -->
          <!-- 打卡成功 end -->

          <!-- 时间、位置信息 begin -->
          <!-- <div class="workclock-info">
            <dl>
              <dt><i class="iconfont iconshijian" /> 时间</dt>
              <dd>18:06</dd>
            </dl>
            <dl>
              <dt><i class="iconfont icondizhi" /> 位置</dt>
              <dd>公司范围内</dd>
            </dl>
          </div> -->
          <!-- 时间、位置信息 end -->

        </van-tab>


        <van-tab>
          <template #title>
            <div class="saletab-flex">
              <div class="saletab-icon"><i class="iconfont icona-baifangkehu2x" /></div>
              <div class="saletab-item">
                <div class="saletab-tit">拜访客户</div>
                <!-- <div class="saletab-state">未打卡</div> -->
              </div>
            </div>
          </template>

          <!-- 拜访客户 begin -->
          <div class="visit-add" @click="dkClick(4)">
            <div class="visit-add-text">新增</div>
          </div>

          <div class="work-current-time">当前时间：<span>{{ nowData }}</span></div>

          <div class="work-address">
            <div class="address-tit"><i class="iconfont icondizhi" />位置</div>
            <div class="address-info">{{ locationMsg.address2 }}</div>
          </div>

          <div class="work-address">
            <div class="address-tit"><i class="iconfont iconlishijilu" />历史<div style="float:right" @click="goMore(3)">更多> </div></div>
            <div v-for="(item,index) of bfjlList" :key="index" class="address-info">{{ index+1 }}、{{ zhTime(item.ccdksj) }} {{ item.ccdkwz }}</div>
          </div>

          <!-- 拜访客户 end -->

        </van-tab>

      </van-tabs>
    </div>
    <!-- 考勤类型弹窗 end -->

    <!--  外出记录弹窗 begin -->
    <van-popup
      v-model="WorkShow"
      closeable
      position="bottom"
      :style="{ height: '90%' }"
    >
      <div class="popup-title">外出记录</div>
      <div class="popup-content full">
        <div class="work-matter">
          <div v-for="(item,index) in dkls" :key="index" class="matter-item" @click="itemClick(item)">
            <div class="add"><span class="tag tag1">外</span>{{ item.ccdkwz }}</div>
            <div class="date">{{ getTimes(item.ccdksj) }}</div>
            <div class="add"><span class="tag tag2">回</span>{{ item.fcdkwz }}</div>
            <div class="date">{{ getTimes(item.fcdksj) }}</div>
          </div>

        </div>
      </div>
    </van-popup>
    <!--  外出记录弹窗 end -->

    <!--  新增拜访客户弹窗 begin -->
    <van-popup
      v-model="VisitShow"
      closeable
      position="bottom"
      :style="{ height: '60%' }"
    >
      <div class="popup-title">新增拜访客户</div>
      <div class="popup-content full">
        <van-field
          v-model="message"
          rows="3"
          autosize
          type="textarea"
          style="border:1px #DCDFE3 solid;border-radius: 4px;margin:20px 0;"
          placeholder="请输入内容"
        />
        <div class="foot-btn noshadow full">
          <ul>
            <li><van-button type="info" round size="large" @click="saveBfkh()">确定</van-button></li>
          </ul>
        </div>
      </div>

    </van-popup>
    <!--  新增拜访客户弹窗 end -->

    <FooterNav ref="FooterNav" />
  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import FooterNav from '@/components/footerNav/footerNav.vue'
import { ImagePreview } from 'vant'
import { formatTimes } from '@/common/js/util'
import { pageRouter } from '@/common/js/mixin.js'
import { mapState } from 'vuex'
export default {
  name: 'Nqdk',
  components: {
    headerBar,
    FooterNav
  },
  mixins: [pageRouter],
  data() {
    return {
      bfjlList: [],
      message: '',
      avatar: require('@/assets/images/default-tx.png'), // 头像地址
      active: 0,
      headerData: {
        title: '内勤打卡'
        // leftUrl: '',
        // leftText: ' '
      },
      amPageData: {
        wz: '',
        sj: ''
      }, // 上午数据
      pmPageData: {
        wz: '',
        sj: ''
      }, // 下午数据
      locationMsg: {
        lng: '',
        lat: '',
        address1: '',
        address2: ''
      },
      nowData: '', // 当前是时间
      dkid: '',
      dkbz: 1, // 打卡标志 1是上班 2是出差
      dklx: 0, // 打卡标志 1是上班 2是出差

      WorkShow: false,
      VisitShow: false,
      dkls: {} // 打开历史
    }
  },
  computed: {
    // 取数据
    ...mapState({
      userInfo: state => state.mh.mhUserInfo
    }),
    dzxz: function() {
      return this.$store.state.erp.dzxz
    }
  },
  created() {
    this.$bus.$on('getAddress', (addre) => {
      console.info('=========================', addre)
      this.locationMsg.address2 = addre
    })
  },
  mounted() {
    this.getLocation()
    const _this = this // 声明一个变量指向Vue实例this，保证作用域一致
    setInterval(() => {
      _this.nowData = _this.getNowTime() // 修改数据date
    }, 1000)
    // this.$nextTick(() => {
    //   console.info('进入页面------------------进入页面')
    //   window.localStorage.setItem('getAddress', '')
    // })
    this.getXsdbKqjl(this.dkbz)
    window['updateLocation'] = function(value) {
      console.info('临时坐标-----', value.lon, value.lat, value.address)
      if (value.lon === '0' || value.lat === '0') {
        // loading.hideLoading()
        _this.$toast('定位数据获取失败！')
      } else {
        _this.locationMsg.lng = value.lon
        _this.locationMsg.lat = value.lat
        // if(_this.dkbz !== 0 && _this.dkbz !==1){
        if (window.localStorage.getItem('getAddress')) {
          console.info('getAddress====222======', window.localStorage.getItem('getAddress'))
          if (_this.dkbz === 1) {
            console.info('getAddress====6666======', window.localStorage.getItem('getAddress'))
            _this.active = 1
            _this.locationMsg.address2 = window.localStorage.getItem('getAddress')
          }
        } else {
          _this.locationMsg.address2 = value.address
        }

        // }else{
        _this.locationMsg.address1 = value.address
        // }
      }
    }
    if (window.localStorage.getItem('getAddress')) {
      console.info('getAddress====222======', window.localStorage.getItem('getAddress'))
      if (this.dkbz === 1) {
        console.info('getAddress====6666======', window.localStorage.getItem('getAddress'))
        this.active = 1
        this.locationMsg.address2 = window.localStorage.getItem('getAddress')
        window.localStorage.setItem('getAddress', '') // 清空数据
      }
    }
    // window.localStorage.setItem('getAddress', e.address)
  },
  methods: {
    goMore(dklx) {
      this.$router.push({ name: 'MoreLsjl', params: { dklx: dklx }})
    },
    zhTime(time) {
      if (time) {
        time = time + ''
        return time.slice(0, 4) + '/' + time.slice(4, 6) + '/' + time.slice(6, 8) + ' ' + time.slice(8, 10) + ':' + time.slice(10, 12) + ':' + time.slice(12, 14)
      }
    },
    getBfjl() {
      this.$store.dispatch('erp/getXsdbKqjlAction', {
        userId: this.userInfo.id,
        ksrq: '', // 开始日期
        jzrq: '', // 截止日期
        dkbz: 3,
        pageno: 1,
        pagerow: 5
      }).then(res => {
        this.bfjlList = res.list ? res.list : []
      })
    },
    saveBfkh() {
      let dz = ''
      if (window.localStorage.getItem('getAddress')) {
        dz = window.localStorage.getItem('getAddress')
      } else {
        if (this.locationMsg.address1) {
          dz = this.locationMsg.address1
        } else {
          this.$toast('获取定位信息失败')
          return
        }
      }
      if (!this.message) {
        this.$toast('请输入拜访内容')
        return
      }
      console.log('dz============================================', dz)
      this.$store.dispatch('erp/submitXsdbSignAction', {
        userId: this.userInfo.id,
        id: '', // 记录
        lkwz: dz,
        fhwz: '',
        bfkhmc: this.message,
        dkbz: 3 // 打卡标志 1是上班 2是出差 3拜访客户
      }).then((res) => {
        this.$toast('保存成功！')
        this.VisitShow = false
      }).catch((error) => {
        this.$toast(error)
      })
    },
    getTimes(kbsj) {
      var time = formatTimes(kbsj)
      return time
    },
    getNowTime() {
      const date = new Date()
      let hours = ''
      let minute = ''
      let second = ''
      if (date.getHours() < 10) {
        hours = '0' + date.getHours()
      } else {
        hours = date.getHours()
      }
      if (date.getMinutes() < 10) {
        minute = '0' + date.getMinutes()
      } else {
        minute = date.getMinutes()
      }
      if (date.getSeconds() < 10) {
        second = '0' + date.getSeconds()
      } else {
        second = date.getSeconds()
      }
      return date.getFullYear() + '年' + (date.getMonth() + 1) + '月' + date.getDate() + '日' + '  ' + hours + ':' + minute + ':' + second
    },
    // 获取时间
    getTime(val, type) {
      // 时分秒 2021 0923 105507
      const data = val + ''
      if (type === 1) {
        const times = data.slice(4)
        const times1 = times.slice(4)
        return this.insertStr(this.insertStr(times1, 2, ':'), 5, ':')
        // return times1
      } else { // 月日
        const times = data.slice(4)
        const times1 = times.slice(0, 4)
        return this.insertStr(times1, 2, '/')
      }
    },
    insertStr(soure, start, newStr) {
      return soure.slice(0, start) + newStr + soure.slice(start)
    },
    // 展示弹出框
    showPopupWork() {
      this.WorkShow = true
    },
    showPopupVisit() {
      this.message = ''
      this.VisitShow = true
    },
    // 跳转地址选择页面
    goAddres() {
      // if (this.locationMsg.lng && this.locationMsg.lat) {
      this.$router.push({
        name: 'Dzxz',
        params: {
          lng: this.locationMsg.lng,
          lat: this.locationMsg.lat
        }
      })
      // } else {
      //   this.$toast('定位数据获取失败！')
      // }
    },
    getLocation() {
      const pin = {
        'type': '1'
      }
      if (process.env.NODE_ENV !== 'development') {
        try {
          window.myapp.getDistance(JSON.stringify(pin))
        } catch (err) {
          console.log(err)
        }
        this.$bridge.callhandler('getDistance', JSON.stringify(pin), (data) => {
          const datas = JSON.parse(data)
          this.locationMsg.lng = datas.lng
          this.locationMsg.lat = datas.lat
          this.locationMsg.address1 = datas.address
          if (window.localStorage.getItem('getAddress')) {
            console.info('getAddress====222======', window.localStorage.getItem('getAddress'))
            if (this.dkbz === 1) {
              console.info('getAddress====6666======', window.localStorage.getItem('getAddress'))
              this.active = 1
              this.locationMsg.address2 = window.localStorage.getItem('getAddress')
            }
          } else {
            this.locationMsg.address2 = datas.address
          }
          console.info('临时坐标-----', datas.lng, datas.lat, datas.address)
          console.info('getDistance------', this.locationMsg.address)
        })
      } else {
        this.$toast('返回原生页面')
      }
    },
    dkClick(bz) {
      this.dklx = bz
      if (bz === 0 || bz === 1) {
        this.dkbz = 1
        if (this.locationMsg.address1) {
          this.submitXsdbSign(this.locationMsg.address1)
        } else {
          this.$toast('获取定位信息失败')
        }
      } else if (bz === 2 || bz === 3) {
        this.dkbz = 2
        if (window.localStorage.getItem('getAddress')) {
          this.submitXsdbSign(window.localStorage.getItem('getAddress'))
        } else {
          if (this.locationMsg.address2) {
            this.submitXsdbSign(this.locationMsg.address2)
          } else {
            this.$toast('获取定位信息失败')
          }
        }
      } else {
        this.message = ''
        this.dkbz = 3
        this.VisitShow = true
      }
    },
    showImage(imagePath) {
      ImagePreview({
        images: [
          imagePath
        ],
        startPosition: 0,
        onClose() {
          // do something
        }
      })
    },
    onClick(name, title) {
      console.info('name=====22=====', name)
      if (name === 0 || name === '0') {
        // if (window.localStorage.getItem('getAddress')) {
        //   window.localStorage.setItem('getAddress', '')
        //   this.locationMsg.address2 = ''
        // }
        this.getXsdbKqjl(1)
      } else {
        this.getBfjl()
      }
    },
    // 获取打卡历史信息
    getXsdbKqjl(dkbz) {
      this.$store.dispatch('erp/getXsdbKqjlAction', {
        userId: this.userInfo.id,
        ksrq: '', // 开始日期
        jzrq: '', // 截止日期
        dkbz: dkbz,
        pageno: 1,
        pagerow: 1
      }).then((res) => {
        console.info('res------', res, this.dkbz)
        if (res && res.list && res.list.length > 0) {
          if (dkbz === 1 && this.active === 0) {
            this.amPageData.wz = res.list[0].ccdkwz
            this.amPageData.sj = res.list[0].ccdksj
            this.pmPageData.wz = res.list[0].fcdkwz
            this.pmPageData.sj = res.list[0].fcdksj
            this.dkid = res.list[0].id
            this.dkls = res.list
          } else {
            this.dkls = res.list
          }
        } else {
          this.dkls = []
        }
      }).catch((error) => {
        this.$toast(error)
      })
    },

    // 上传签到信息
    submitXsdbSign(dz) {
      console.info('dz---------', dz)
      console.info('this.dkbz---------', this.dkbz)
      this.$store.dispatch('erp/submitXsdbSignAction', {
        userId: this.userInfo.id,
        id: this.dkid, // 记录
        lkwz: this.dklx === 0 || this.dklx === 2 ? dz : '', // 离开打卡位置
        fhwz: this.dklx === 1 || this.dklx === 3 ? dz : '', // 返回的打卡位置
        dkbz: this.dkbz // 打卡标志 1是上班 2是出差
      }).then((res) => {
        this.$toast.success('上传成功！')
        this.amPageData.wz = res.ccdkwz
        this.amPageData.sj = res.ccdksj
        this.pmPageData.wz = res.fcdkwz
        this.pmPageData.sj = res.fcdksj
        this.dkid = res.id
      }).catch((error) => {
        this.$toast(error)
      })
    },
    // 3、根据记录id删除打卡记录
    deleteXsdbSign() {
      this.$store.dispatch('erp/deleteXsdbSignAction', {
        id: ''
        // userId: this.userInfo.id,
        // ksrq: '', // 开始日期
        // jzrq: '', // 截止日期
        // dkbz: '' // 打卡标志 1是上班 2是出差
      }).then((res) => {
        console.info('res------', res)
      }).catch((error) => {
        this.$toast(error)
      })
    },
    itemClick(item) {
      this.WorkShow = false
      console.info(item)
      this.amPageData.wz = item.ccdkwz
      this.amPageData.sj = item.ccdksj
      this.pmPageData.wz = item.fcdkwz
      this.pmPageData.sj = item.fcdksj
      this.dkid = item.id
    }

  }
}
</script>

<style scoped>

</style>
